import "./NewExpenses.css";
import ExpenseItem from "./ExpenseItem";
function NewExpenses(props){
    const expensesFirst=props.expenses[0];
    const expensesSecond=props.expenses[1];
    const expensesThird=props.expenses[2];
    const expensesFourth=props.expenses[3];
return (
    <div className="expenses">
      <ExpenseItem title={expensesFirst.title} amount={expensesFirst.amount} date={expensesFirst.date}/>
      <ExpenseItem title={expensesSecond.title} amount={expensesSecond.amount} date={expensesSecond.date}/>
      <ExpenseItem title={expensesThird.title} amount={expensesThird.amount} date={expensesThird.date}/>
      <ExpenseItem title={expensesFourth.title} amount={expensesFourth.amount} date={expensesFourth.date}/>
    </div>
    );
}

export default NewExpenses;